"use client";

import { useState } from "react";
import { useForm, useFieldArray } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import {
  Form,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
  FormControl,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { PlusCircle, Trash2 } from "lucide-react";
import { toast } from "sonner";
import { productSchemaZ } from "@/lib/schemas";
import axios from "axios";
import useCategories from "../../categories/_hooks/useCategories";
import Image from "next/image";

export default function ProductCreateForm() {
  const [isLoading, setIsLoading] = useState(false);
  const [mainFiles, setMainFiles] = useState<File[]>([]);

  const form = useForm({
    resolver: zodResolver(productSchemaZ),
    defaultValues: {
      title: "",
      slug: "",
      description: "",
      categories: [""],
      variants: [
        {
          size: "",
          color: "",
          stock: 0,
          price: 0,
          files: [] as File[],
        },
      ],
      fabric: "",
      valueAddition: "",
      cutFit: "",
      collarNeck: "",
      sleeve: "",
      length: "",
      washCare: "",
      sideCut: "",
      isFeatured: false,
      isActive: true,
      tags: [""],
    },
    mode: "onChange",
  });

  const {
    fields: variantFields,
    append: appendVariant,
    remove: removeVariant,
  } = useFieldArray({ control: form.control, name: "variants" });

  const handleMainFilesChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) return;
    setMainFiles((prev) => [...prev, ...Array.from(e.target.files ?? [])]);
  };

  const onSubmit = async (data: any) => {
    try {
      setIsLoading(true);

      // Prepare all files: main + variant
      const formData = new FormData();
      mainFiles.forEach((file) => formData.append("images", file));

      data.variants.forEach((variant: any, idx: number) => {
        variant.files?.forEach((file: File) =>
          formData.append(`variantImages[${idx}]`, file)
        );
        delete variant.files; // remove from body, backend handles separately
      });

      Object.entries(data).forEach(([key, value]) => {
        if (key !== "variants") formData.append(key, value);
      });

      formData.append("variants", JSON.stringify(data.variants));

      const res = await axios.post("/api/products", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });

      if (!res.data.success)
        throw new Error(res.data.error?.message || "Error");

      toast.success(res.data.success.message);
      form.reset();
      setMainFiles([]);
    } catch (err: any) {
      toast.error(err.message || "Unexpected error");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
        {/* Title & Slug */}
        <div className="grid gap-4 md:grid-cols-2">
          <FormField
            control={form.control}
            name="title"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Product Title</FormLabel>
                <FormControl>
                  <Input placeholder="e.g., 3 Piece Set" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="slug"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Product Slug</FormLabel>
                <FormControl>
                  <Input placeholder="e.g., three-piece-set" {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        {/* Description */}
        <FormField
          control={form.control}
          name="description"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Description</FormLabel>
              <FormControl>
                <Textarea placeholder="Detailed description..." {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        {/* Main Product Images */}
        <div>
          <FormLabel>Product Images</FormLabel>
          <Input type="file" multiple onChange={handleMainFilesChange} />
          {mainFiles.length > 0 && (
            <div className="mt-2 flex flex-wrap gap-2">
              {mainFiles.map((file, idx) => (
                <div
                  key={idx}
                  className="relative w-20 h-20 border rounded-md overflow-hidden flex items-center justify-center bg-gray-100"
                >
                  <Image
                    src={URL.createObjectURL(file)}
                    alt={file.name}
                    width={64}
                    height={64}
                    className="object-cover"
                  />
                  <Button
                    variant="destructive"
                    onClick={() => {
                      const newFiles = [...mainFiles];
                      newFiles.splice(idx, 1);
                      setMainFiles(newFiles);
                    }}
                    className="absolute top-0 right-0 p-1 h-6 w-6 text-xs"
                  >
                    ×
                  </Button>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Variants */}
        <div>
          <h3 className="text-lg font-medium">Product Variants</h3>
          {variantFields.map((variant, index) => {
            const [variantFiles, setVariantFiles] = useState<File[]>([]);

            const handleVariantFiles = (
              e: React.ChangeEvent<HTMLInputElement>
            ) => {
              if (!e.target.files) return;
              setVariantFiles((prev) => [
                ...prev,
                ...Array.from(e.target.files ?? []),
              ]);
              form.setValue(`variants.${index}.files`, [
                ...(form.getValues(`variants.${index}.files`) || []),
                ...Array.from(e.target.files ?? []),
              ]);
            };

            return (
              <div key={variant.id} className="border p-4 rounded-md mt-4">
                {/* Color & Size */}
                <div className="grid gap-4 md:grid-cols-2">
                  <FormField
                    control={form.control}
                    name={`variants.${index}.color`}
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Color</FormLabel>
                        <FormControl>
                          <Input placeholder="e.g., Black" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name={`variants.${index}.size`}
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Size</FormLabel>
                        <FormControl>
                          <Input placeholder="e.g., M" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </div>

                {/* Stock & Price */}
                <div className="grid gap-4 md:grid-cols-2 mt-4">
                  <FormField
                    control={form.control}
                    name={`variants.${index}.stock`}
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Stock</FormLabel>
                        <FormControl>
                          <Input type="number" placeholder="0" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                  <FormField
                    control={form.control}
                    name={`variants.${index}.price`}
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>Price</FormLabel>
                        <FormControl>
                          <Input type="number" placeholder="0.00" {...field} />
                        </FormControl>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                </div>

                {/* Variant Images */}
                <div className="mt-4">
                  <FormLabel>Variant Images</FormLabel>
                  <Input type="file" multiple onChange={handleVariantFiles} />
                  {variantFiles.length > 0 && (
                    <div className="mt-2 flex flex-wrap gap-2">
                      {variantFiles.map((file, idx) => (
                        <div
                          key={idx}
                          className="relative w-20 h-20 border rounded-md overflow-hidden flex items-center justify-center bg-gray-100"
                        >
                          <Image
                            src={URL.createObjectURL(file)}
                            alt={file.name}
                            width={64}
                            height={64}
                            className="object-cover"
                          />
                          <Button
                            variant="destructive"
                            onClick={() => {
                              const newFiles = [...variantFiles];
                              newFiles.splice(idx, 1);
                              setVariantFiles(newFiles);

                              const currentFiles =
                                form.getValues(`variants.${index}.files`) || [];
                              currentFiles.splice(idx, 1);
                              form.setValue(
                                `variants.${index}.files`,
                                currentFiles
                              );
                            }}
                            className="absolute top-0 right-0 p-1 h-6 w-6 text-xs"
                          >
                            ×
                          </Button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>

                {/* Remove Variant */}
                <Button
                  type="button"
                  variant="destructive"
                  onClick={() => removeVariant(index)}
                  disabled={variantFields.length === 1}
                  className="mt-4"
                >
                  <Trash2 className="mr-2 h-4 w-4" /> Remove Variant
                </Button>
              </div>
            );
          })}

          <Button
            type="button"
            variant="outline"
            onClick={() =>
              appendVariant({
                size: "",
                color: "",
                stock: 0,
                price: 0,
                files: [] as File[],
              })
            }
            className="mt-4"
          >
            <PlusCircle className="mr-2 h-4 w-4" /> Add Variant
          </Button>
        </div>

        {/* Submit */}
        <Button type="submit" disabled={isLoading}>
          {isLoading ? "Creating..." : "Create Product"}
        </Button>
      </form>
    </Form>
  );
}
